import React, { Component } from "react";
import TodoField from "./TodoField";
import TodoList from "./TodoList";

export default class Todo extends Component {
  state = {
    items: [
      {
        key: 12121,
        text: "You can put Some notes here during watching videos.."
      }
    ],
    currentItem: {
      text: "",
      key: ""
    },
    isClicked: false
  };
  delItem = keyid => {
    const filteredItems = this.state.items.filter(item => item.key !== keyid);
    this.setState({ items: filteredItems });
  };
  addItem = e => {
    e.preventDefault();
    const newItem = this.state.currentItem;
    if (newItem.text !== "") {
      const items = [...this.state.items, newItem];
      console.log(items);
      this.setState({
        items: items,
        currentItem: { text: "", key: "" }
      });
    }
  };
  formOnChange = e => {
    const item = e.target.value;
    this.setState({ currentItem: { text: item, key: Date.now() } });
  };
  itemOnClick = e => {
    console.log("item clicked");
    this.setState(pervState => ({
      isClicked: !pervState.isClicked
    }));
  };
  render() {
    return (
      <div>
        <p>click to mark as complete</p>
        <TodoList
          itemOnClick={this.itemOnClick}
          delBtnClick={this.delItem}
          isClicked={this.state.isClicked}
          items={this.state.items}
        />
        <TodoField
          formOnSubmit={this.addItem}
          fieldValue={this.state.currentItem}
          fieldOnChange={this.formOnChange}
        />
      </div>
    );
  }
}
