import React, { Component } from "react";
import { BrowserRouter, Route, Redirect, Switch } from "react-router-dom";
import Home from "./components/Home";
import Profile from "./components/Profile";
import Courses from "./components/Courses";
import LearnVid from "./components/LearnVid";
import axios from "axios";
import Temp from "./components/Temp";
import Todo from "./components/Todo/Todo";

export default class routes extends Component {
  state = {
    vidz: [],
    pageSize: 4,
    err: "",
    loading: true
  };
  componentDidMount = () => {
    axios
      .get(
        "https://www.googleapis.com/youtube/v3/playlistItems?part=contentDetails&maxResults=16&playlistId=UU29ju8bIPH5as8OGnQzwJyA&key=AIzaSyBYu_83sXqggaOIRuCBKrQx_Cn-BdWkzIU"
      )
      .then(res => {
        const vidz = res.data.items;
        this.setState({ vidz });
      })
      .catch(err => this.setState({ err: err.message }));
  };
  render() {
    return (
      <BrowserRouter>
        <Switch>
          <Route path="/profile" component={Profile} />
          <Route
            path="/vid/:id"
            render={props => <LearnVid {...props} stateProp={this.state} />}
          />
          <Route
            path="/courses/:cat/:id"
            render={props => <Temp {...props} stateProp={this.state} />}
          />
          <Route path="/courses" component={Courses} />
          <Route
            path="/eduapp"
            render={props => <Home {...props} stateProp={this.state} />}
          />
          <Route
            path="/todo"
            render={props => <Todo {...props} stateProp={this.state} />}
          />
          <Redirect from="/" to="/eduapp" />
        </Switch>
      </BrowserRouter>
    );
  }
}
