import React, { Component } from "react";
import { Container, Row, Col, Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import Navigation from "./Navigation";
import Pagination from "../utils/pagination";
import { paginate } from "../utils/paginate";
import Logo from "./Logo";
import Notice from "./Notice";
import axios from "axios";
import Spinner from "react-spinkit";
import Todo from "./Todo/Todo";
class Courses2 extends Component {
  state = {
    catslist: [],
    currentId: "",
    loading: true,
    currentPage: 1
  };
  componentDidMount = () => {
    const currentId = this.props.stateProp.match.params.id;
    this.setState({ currentId });
    axios
      .get(
        `https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&maxResults=50&playlistId=${currentId}&key=AIzaSyBYu_83sXqggaOIRuCBKrQx_Cn-BdWkzIU`
      )
      .then(res => {
        const catslist = res.data.items;
        this.setState({ catslist, loading: false });
      })
      .catch(err => this.setState({ err: err.message }));
  };
  handlePageChange = (e, page) => {
    e.preventDefault();
    this.setState({ currentPage: page });
  };
  render() {
    let catslist;
    this.state.catslist.constructor !== Array
      ? (catslist = this.state.catslist.toString())
      : (catslist = paginate(this.state.catslist, 6, this.state.currentPage));

    return (
      <Container>
        <Row className="app">
          <Col sm={1} className="sm-side">
            <Logo />
            <Navigation />
          </Col>
          <Col sm={8}>
            <Notice />
            <h2>Videos:</h2>
            {this.state.loading ? (
              <Spinner
                className="loading text-center"
                name="three-bounce"
                color="orange"
                fadeIn="none"
              />
            ) : null}
            <div className="home-grid">
              {catslist.map(m => (
                <Card key={m.id}>
                  <Link to={`/vid/${m.snippet.resourceId.videoId}`}>
                    <Card.Img
                      variant="top"
                      src={m.snippet.thumbnails.standard.url}
                    />
                    <Card.Body>
                      <Card.Title>{m.snippet.title}</Card.Title>

                      <Card.Text style={{ textAlign: "justify" }}>
                        {m.snippet.description.length > 10
                          ? m.snippet.description.substr(0, 700).concat("...")
                          : null}
                      </Card.Text>
                    </Card.Body>
                  </Link>
                </Card>
              ))}
            </div>
            <Pagination
              pageSize={6}
              totalItems={this.state.catslist.length}
              onPageChange={this.handlePageChange}
              currentPage={this.state.currentPage}
            />
          </Col>
          <Col sm={3} className="side">
            <div className="logo">
              <p>LOGO</p>
            </div>
            <h3>Notes:</h3>
            <Todo />
          </Col>
        </Row>
        <Row className="footer">
          <Col>Hello</Col>
        </Row>
      </Container>
    );
  }
}

export default Courses2;
