import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Todo from "./Todo/Todo";
import Navigation from "./Navigation";
import Logo from "./Logo";
import Notice from "./Notice";
import CourseCat from "./CourseCat";
import axios from "axios";

class Courses extends Component {
  state = {
    catslist: []
  };
  componentDidMount = () => {
    axios
      .get(
        "https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&maxResults=50&playlistId=PL8dPuuaLjXtOeEc9ME62zTfqc0h6Pe8vb&key=AIzaSyBYu_83sXqggaOIRuCBKrQx_Cn-BdWkzIU"
      )
      .then(res => {
        const catslist = res.data.items;
        this.setState({ catslist });
      })
      .catch(err => this.setState({ err: err.message }));
  };

  render() {
    return (
      <Container>
        <Row className="app">
          <Col sm={1} className="sm-side">
            <Logo />
            <Navigation />
          </Col>
          <Col sm={8}>
            <Notice />
            <h2>Courses:</h2>
            <CourseCat />
          </Col>
          <Col sm={3} className="side">
            <div className="logo">
              <p>LOGO</p>
            </div>
            <h3>Notes:</h3>
            <Todo />
          </Col>
        </Row>
        <Row className="footer">
          <Col>Hello</Col>
        </Row>
      </Container>
    );
  }
}

export default Courses;
