import React from "react";
import Styled from "styled-components";
const TodoField = props => {
  return (
    <div className="todo-field text-center">
      <form onSubmit={e => props.formOnSubmit(e)}>
        <TodoFieldStyled
          onChange={e => props.fieldOnChange(e)}
          value={props.fieldValue.text}
          placeholder="Write Your Note Here..."
          cols="30"
          rows="5"
        />
        <button
          className="btn btn-block btn-primary mb-3"
          type="submit"
          style={{ width: "100%" }}
        >
          Submit
        </button>
      </form>
    </div>
  );
};

const TodoFieldStyled = Styled.textarea`
  background-color: #eee;
  border-radius: 10px;
  padding: .5rem;
`;

export default TodoField;
