import React from "react";
import { Link } from "react-router-dom";
const HomeTiles = ({ stateProp, vidz }) => {
  return (
    <>
      <div className="home-grid">
        {stateProp.err
          ? `${
              stateProp.err
            }. Plese check your connection and youtube accessibility`
          : vidz.map((m, i) => (
              <div className="thumb" key={m.id}>
                <Link to={`/vid/${m.contentDetails.videoId}`}>
                  <img
                    src={`https://img.youtube.com/vi/${
                      m.contentDetails.videoId
                    }/0.jpg`}
                    alt=" "
                  />
                </Link>
              </div>
            ))}
      </div>
    </>
  );
};

export default HomeTiles;
