import React from "react";
import { NavLink } from "react-router-dom";

export default function Icon(props) {
  return (
    <div className={`iconBox ${props.extraClass}`}>
      <NavLink to={props.linkurl} className="icon" activeClassName="selected">
        <i className={`fas fa-${props.iconName}`} />
        {props.text}
      </NavLink>
    </div>
  );
}
