import React, { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Navigation from "./Navigation";
import Pagination from "../utils/pagination";
import { paginate } from "../utils/paginate";
import HomeTiles from "./HomeTiles";
import HomeBanner from "./HomeBanner";
import "../assets/Styles.css";
import "../assets/temp.css";
import Logo from "./Logo";
import Notice from "./Notice";
import Todo from "./Todo/Todo";
export default function Home(props) {
  const [currentPage, setCurrentPage] = useState(1);
  const handlePageChange = (e, page) => {
    e.preventDefault();
    setCurrentPage(page);
  };
  let vidz;
  props.stateProp.vidz.constructor !== Array
    ? (vidz = props.stateProp.vidz.toString())
    : (vidz = paginate(
        props.stateProp.vidz,
        props.stateProp.pageSize,
        currentPage
      ));

  return (
    <Container>
      {console.log(vidz)}
      <Row className="app">
        <Col sm={1} className="sm-side">
          <Logo />
          <Navigation />
        </Col>
        <Col sm={8}>
          <Notice />

          <HomeBanner />
          <HomeTiles stateProp={props.stateProp} vidz={vidz} />
          <Pagination
            pageSize={props.stateProp.pageSize}
            totalItems={props.stateProp.vidz.length}
            onPageChange={handlePageChange}
            currentPage={currentPage}
          />
        </Col>
        <Col sm={3} className="side">
          <div className="logo">
            <p>LOGO</p>
          </div>
          <h3>Notes:</h3>
          <Todo />
        </Col>
      </Row>
      <Row className="footer">
        <Col>Hello</Col>
      </Row>
    </Container>
  );
}
