import React, { useState } from "react";
import Spinner from "react-spinkit";
// import vidJpg from "../assets/600400.jpg";
function Course(props) {
  const [loading, setLoading] = useState(true);
  const hideSpinner = () => {
    setLoading(false);
  };
  return (
    <div className="main">
      <div className="vid">
        {loading ? (
          <Spinner
            className="loading text-center"
            name="three-bounce"
            color="orange"
            fadeIn="none"
          />
        ) : null}
        <iframe
          title="yt"
          width="560"
          height="315"
          src={`https://www.youtube.com/embed/${props.idman}`}
          onLoad={hideSpinner}
          frameBorder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        />
      </div>
      <div className="short-desc">{props.desc}</div>
    </div>
  );
}

export default Course;
